.input {
  font-size: 15px;
  width: 300px;
  height: 53px;
  border: 2px solid rgb(226, 226, 226);
  border-radius: 3px;
  padding-left: 30px;
}

.button {
  font-size: 15px;
  width: 150px;
  margin: 0;
  margin-left: 20px;
  display: inline-block;
  padding: 20px;
  background-color: #fff;
  border: 2px solid rgb(226, 226, 226);
  border-radius: 3px;
  transition: background-color 300ms ease;
}

.button:hover {
  background-color: rgb(203, 189, 255);
}
